.results{
    position: relative;
    padding-top: 50px;
}

.container{
    max-width: 1170px;
    margin: 0px auto;
    padding: 0px 15px;
}

.resultGrid{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
}

.callToForm{
    padding: 55px 0px 100px;
}

.pageTitle h2{
    font-weight: 400;
font-size: 33px;
line-height: 120%;
letter-spacing: 0.01em;
margin: 0;
color: #6243CE;
padding-bottom: 20px;
display: flex;
align-items: center;
}
.pageTitle h2 img{
margin-left: 10px;
}
.pageTitle p{
    font-weight: 500;
font-size: 16px;
line-height: 24px;
letter-spacing: -0.01em;
color: #333333;
margin: 0;
padding-bottom: 30px;
}




@media screen and (max-width:991px){
    
    .results {
        padding-top: 24px;
    }
    
    .pageTitle h2 {
        font-size: 30px;
        line-height: 120%;
        justify-content: center;
    }
    
    .pageTitle p{
        text-align: center;
        padding-bottom: 20px;
    }

    .callToForm {
        padding: 48px 0px 48px;
    }

    }



