.sidebar{
    position: relative;
    max-width: 316px;
    width: 100%;
    background: #F5F8F8;
    border-radius: 10px;
    margin-top: 35px;
}

.report{
    width: calc(100% - 336px);
}

.sidebarGrid p {
    font-weight: 500;
font-size: 14px;
line-height: 24px;
margin: 0;
letter-spacing: -0.01em;
text-transform: uppercase;
padding-bottom: 15px;
color: #333333;
position: absolute;
top: -40px;
left: 0
}

.sidebarGrid ul {
    margin: 0;
    padding: 20px;
    list-style-type: none;
}

.sidebarGrid ul li span {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
/* identical to box height, or 150% */
    letter-spacing: -0.01em;
    font-feature-settings: 'calt' off;
    color: #333333;
    display: flex;
    align-items: center;
    padding-bottom: 5px;
}

.sidebarGrid ul li span img {
    margin-left: 10px;
}

.sidebarGrid ul li b img {
    margin-left: 10px;
    display: none;
}


.sidebarGrid ul li {
    margin-bottom: 10px;
}

.sidebarGrid ul li b {
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.01em;
    color: #333333;
}

.resubmit{
    padding: 0px 20px 10px;
}

.resubmit a{
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.01em;
    display: block;
    color: #F35C5C;
    text-decoration: none;
}





.reportBox, .report{
    position: relative;
}

.reportGrid p{
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    margin: 0;
    letter-spacing: -0.01em;
    text-transform: uppercase;
    padding-bottom: 15px;
    color: #333333;
}

.reportCard{
    background: #FFFFFF;
    box-shadow: 0px 2px 27px -1px rgba(0, 0, 0, 0.12);
    border-radius: 10px;
    padding: 20px;
    margin-bottom: 15px;
    position: relative;
}

.reportCard.blurred::before {
    content: "";
    background: rgba(255, 255, 255, 0.6);
    backdrop-filter: blur(3.5px);
    border-radius: 10px;
    width: 100%;
    height: 125px;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 99;
}

.reportBox .reportCard:last-child{
    margin: 0;
}

.reportCardTitle h4{
    font-weight: 400;
    font-size: 26px;
    line-height: 120%;
    padding-bottom: 10px;
    letter-spacing: 0.01em;
    margin: 0;
    color: #333333;
}

.reportCardTitle span{
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    margin: 0;
    letter-spacing: -0.01em;
    padding-bottom: 5px;
    color: #333333;
    display: block;
}

.reportGrid .reportCardTitle p{
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.01em;
    padding: 15px 0px 20px ;
    color: #333333;
    margin: 0;
    text-transform: none;
}


.reportLoader{
    background: #2CD674;
    box-shadow: 0px 3px 6px rgba(44, 214, 116, 0.36);
    border-radius: 30px;
    max-width: 240px;
    height: 17px;
}

.reportCardFooter{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}
.reportCardFooter p{
    background: rgba(206, 208, 208, 0.4);
    border-radius: 6px;
    padding: 6px;
    width: calc(100% - 240px);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    margin: 0;
    letter-spacing: -0.01em;
    color: #333333;
    text-transform: none;
}

.reportCardFooter a{
    max-width: 152px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid #F35C5C;
    border-radius: 74px;
    padding: 10px 20px;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.01em;
    color: #F35C5C;
    text-decoration: none;
}



.formContainer{
    max-width: 962px;
    margin: 0px auto;
}

.formTitle h3{
margin: 0;
font-weight: 400;
font-size: 26px;
line-height: 120%;
letter-spacing: 0.01em;
padding-bottom: 22px;
color: #6243CE;
display: flex;
align-items: center;
justify-content: center;
}

.formTitle h3 img{
margin-right: 20px;
}
.formTitle p {
margin: 0;
font-weight: 500;
font-size: 16px;
line-height: 140%;
text-align: center;
letter-spacing: -0.01em;
color: #333333;
padding-bottom: 40px;
}

.formGrid{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}

.formBox{
max-width: 394px;
width: 100%;
padding: 40px 30px;
background: #FFFFFF;
border: 2px solid rgba(98, 67, 206, 0.18);
box-shadow: 0px 2px 27px -1px rgba(0, 0, 0, 0.12);
border-radius: 10px;
}


.formBox form{
position: relative;
}

.formBox input {
    background: #FFFFFF;
    border: 2px solid #EEE9FF;
    border-radius: 51px;
    margin-bottom: 10px;
    width: 100%;
    padding: 9px 20px;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: -0.01em;
    box-sizing: border-box;
    font-family: 'Raleway', sans-serif;

}

.formBox input:focus {
outline: none;
}

.formBox input::placeholder {
    color: #6E6F6F;
}

.formBox button {
    background: #F35C5C;
    border-radius: 63px;
    padding: 19px 20px;
    width: 100%;
    font-weight: 700;
    font-size: 16px;
    line-height: 100%;
    color: #FFFFFF;
    border: 1px solid #F35C5C;
    cursor: pointer;
}

.formBox label {
    font-weight: 400;
    font-size: 13px;
    line-height: 140%;
    display: flex;
    color: #727C88;
    margin-top: 10px;
    letter-spacing: 0.5px;
}

.formBox label span a{
    color: #727C88;
}

.formBox input[type="checkbox"] {
width: 18px;
height: 18px;
margin: 0;
margin-right: 15px;
border: 1px solid #6243CE;
cursor: pointer;

}

.formContent{
position: relative;
width: calc(100% - 520px);
}

.formContent ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
    display: flex;
    flex-direction: column;
    gap: 28px;
}

.formContent ul li{
    font-weight: 700;
    font-size: 16px;
    line-height: 172.5%;
    letter-spacing: -0.01em;
    position: relative;
    color: #333333;
    padding-left: 35px;
}

.formContent ul li::before{
    content: "";
    background: url(../../assets/results/check1.svg);
    width: 22px;
    height: 18px;
    display: block;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    background-size: cover;
}


/****************** Media Query *********************/


@media screen and (max-width:991px){


.formContent{
    width: calc(100% - 400px);
}
.formBox {
    max-width: 300px;
}

.sidebar {
    max-width: 100%;
    
    margin-bottom: 21px;

}
.report {
    width: 100%;
} 
    
.reportCardFooter p {
    width: 100%;
    margin-bottom: 10px;
}


.results {
    padding-top: 24px;
}

.pageTitle h2 {
    font-size: 30px;
    line-height: 120%;
    justify-content: center;
}


.sidebarGrid ul li span {
    display: none;
}

.sidebarGrid ul li b {
    font-weight: 700;
    font-size: 14px;
    line-height: 120%;
}

.sidebarGrid ul {
    padding: 20px 20px 0px;

}


.reportCardTitle h4 {
    font-size: 22px;
    line-height: 120%;
}
.reportGrid .reportCardTitle p {
    padding: 15px 0px 10px;
}

.reportCardFooter a {
    max-width: 100%;
}

.formTitle h3 {
    flex-direction: column;
    text-align: center;
    gap: 10px;
    font-size: 30px;
    line-height: 120%;
}

.formTitle h3 img {
    margin: 0;
}

.formTitle p {
    padding-bottom: 30px;
}
.formContent ul {

    gap: 10px;
}

.sidebarGrid ul li b img {
    display: inline;
}


}


@media screen and (max-width:767px){


.formGrid {
    gap: 30px;
}

.formBox {
    max-width: 100%;
    margin: 0px auto;
    padding: 30px 15px;
    order: 2;
}
.formContent {
    width: 100%;
    order: 1;
}


}